
import axios from "axios";

const getMenu = async () => {
    return await axios.post("/menuList.do",{"sysGroup":"CAR"})
    .then((res) => {
      console.log('조회결과',res.data.RESULT_LIST);
      return res.data.RESULT_LIST
    })
    .catch((res) => {
      console.error("에러", res);
      return 'ERROR'
    });
      
  } 
export default getMenu