<script setup>
import BaseHeader from '../components/common/header/baseHeader.vue'
import BaseFooter from '../components/common/footer/baseFooter.vue'
import { useStore } from "vuex";
import axios from "axios";

const store = useStore();

function fn_test() {
    store.commit("changeStatus", true);
    const timer = setTimeout(() => {
      store.commit("changeStatus", false);
    }, 2000);

    console.log('end loadinbar');
    return () => clearTimeout(timer);
}

const fn_ajax = async () => {
  store.commit("changeStatus", true);
  await axios.post("/boardList.do",{})
  .then((res) => {
    console.log('조회결과',res.data.RESULT_LIST);
    return res.data.RESULT_LIST
  })
  .catch((res) => {
    console.error("에러", res);
    return 'ERROR'
  });
  store.commit("changeStatus", false);
      
} 

</script>

<script>
export default {
  name: 'sampleBase',
}

</script>

<template>
<div>
  <BaseHeader title="샘플페이지"></BaseHeader>
  <main>
    <div>
      본문 sample<br>
      <button type="button" id="s2" @click="fn_test">로딩테스트</button><br>
      <button type="button" id="s2" @click="fn_ajax">AJAX테스트</button><br>
    </div>
  </main>
  <BaseFooter></BaseFooter>
</div>
</template>

