
<script setup>
import { inject, ref,onMounted, defineProps } from 'vue'
import { useRoute,useRouter } from 'vue-router'
//import axios from "axios";

const router = useRouter()
const route = useRoute()

console.log('route info',route.fullPath)
console.log('router info',router.getRoutes())
//router.addRoute({ path: '/cloth', component: cloth   })
//console.log('ddd',router.getRoutes())

  const props = defineProps(['title'])

  const menuList = ref();
  const show = ref(false)

  let retHtml = '';

  onMounted( async() => {
      console.log(`baseHeader onMount 이벤트`)
      menuList.value = inject('$dbMenu');
//      menuList.value = await getMenu();
//      console.log('onMount 조회',menuList.value);

      if( menuList.value != 'ERROR') {
        let topUrl = '';
        menuList.value.map((item) => {	
          if (topUrl === '') {
            topUrl = item.URL;
            retHtml += '<ul>';
            retHtml += item.MENU_NAME;
          } else if (topUrl === item.PARENT_URL) { 
            retHtml += '<li>';
              if (item.LINK_YN === 'Y') {
                retHtml += '<a href="'+item.URL+'" class="">';
              } 
            retHtml += item.MENU_NAME;
              if (item.LINK_YN === 'Y') {
                retHtml += '</a>'
              } 
              retHtml += '</li>';
          } else if (topUrl != item.PARENT_URL) { 
            topUrl = item.URL;
            retHtml += '</ul>';
            retHtml += '<ul>';
            retHtml += item.MENU_NAME;
          }
        });
        retHtml +='</ul>'; 
      }
//      console.log('결과',retHtml);
  });
/*
  const getMenu = async () => {
    return await axios.post("/menuList.ajax",{})
    .then((res) => {
      console.log('조회결과',res.data.RESULT_LIST);
      return res.data.RESULT_LIST
    })
    .catch((res) => {
      console.error("에러", res);
      return 'NONE';
    });
  }
  console.log(`스크립트 종료`)
  */   
</script>

<template>
    <header>
      <div>
        <button type="button" @click="show=false;this.$router.go(-1)">뒤로</button>
      </div>
      <div>
        <h1>{{props.title}}</h1>
      </div>
      <div>
        <button type="button" class="hambuger" @click="show=!show" title="메뉴"></button>
      </div>
    </header>

    <div :class="show ? 'menu-show' : 'menu-hide'" v-html="retHtml"></div>

</template>

<style>
header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: 65px;
	padding: 1rem;
	color: white;
	background: #36acac;
	font-weight: bold;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.hambuger {
  background-image: url(../../../assets/images/comm/hambuger_menu.png);
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  height: 40px;
  padding-left: 20px;
  vertical-align: middle;  
}

.menu-show {
	width: 100%;
	height: 100%;
	background: #fff;
	position: absolute;
	top:55px;
	left: 0px;
	transition: 1s;
	color: #777;
}

.menu-hide {
	width: 0%;
	height: 100%;
	position: absolute;
	top:55px;
	left: -376px;
	transition: 1s;
	z-index: -1;
}
</style>