import { createRouter, createWebHistory } from 'vue-router';
import MainIndex from './main.vue'
import BasePractice from './sample/basePractice.vue'
import SampleBase from './sample/sampleBase.vue'

const routes = [
    { path: "/", redirect:"/main" },
    { path: "/main", component: MainIndex, },
    { path: "/base", component: BasePractice, },
    { path: "/sample", component: SampleBase, },
    { path: "/cloth", component:() => import('@/views/category/cloth.vue'), },
];

const menuJs = createRouter({
    history: createWebHistory(),
    routes,
})
console.log('router.js');
export default menuJs