
<script setup>
import BaseHeader from '../components/common/header/baseHeader.vue'
import BaseFooter from '../components/common/footer/baseFooter.vue'
import { ref,reactive, computed, onBeforeMount, onMounted, onBeforeUpdate, onUpdated, onUnmounted, watch } from 'vue'

  const cells = Array.from(Array(5).keys()).map((i) =>
    Array.from(Array(10).keys()).map((i) => '')
  )
  console.log(cells)
  const cols = cells.map((_, i) => String.fromCharCode(65 + i))
  
console.log(cols)
  onBeforeMount(() => {
    console.log(`컴포넌트가 마운트 전 이벤트입니다.`)
  })

  onMounted(() => {
    console.log(`컴포넌트가 마운트 됐습니다.`,this)
  })

  onBeforeUpdate(() => {
    console.log(`컴포넌트가 업데이트 전 이벤트입니다.`)
  })

  onUpdated(() => {
    console.log(`컴포넌트가 업데이트 됐습니다.`)
  })

  onUnmounted(() => {
    console.log(`컴포넌트가 언마운트 됐습니다.`)
  })

  const temp = ref(0);
  console.log(temp);
  console.log(temp.value);

  watch(temp, (newTemp, oldTemp) => {
    console.log(newTemp + '-' + oldTemp);
  })

  const fn_increase  =() => {
    temp.value++;
  }

  function fn_selectList(ttt) {
    console.log(ttt);
  }

  function fn_test() {
    alert('test');
  }
  const state = reactive({ count: 0 });
  const map = reactive(new Map([['count', ref(0)]]));

  const showFlag = ref(true);

  const items = [{ message: 'PM' }, { message: 'GROUP' }, { message: 'KOREA' }];

  const fileterArray = computed(() => {
    return items.filter((i) => i.message === 'PM' )
  })
/* length 샘플
  const fileterArray = computed(() => {
    return items.filter((i) => i.message.length > 3 )
  })
*/
/* 배열 상태변경 감지 함수들
push()
pop()
shift()
unshift()
splice()
sort()
reverse()
*/
  //const 의 차이점
  //위에서 일반 배열로 선언한 const items 는 그냥 상수. 재 할당 불가.
  //아래 items2는 ref 객체. 해당 객체의 setter 호출이므로 값 변경 가능
  const items2 = ref([{ message: 'PM' }, { message: 'GROUP' }, { message: 'KOREA' }]);
  const initArray = () => {
    items2.value = items2.value.filter((i) => i.message === 'PM' );
  }

  //입력값 컨트롤
  const message = ref('')
  const checked = ref(true)
  const checkedNames = ref([])
  const picked = ref('')
  const selected = ref('')

  const selected2 = ref('')

  const options = ref([
    { text: '선택하세요', value: '' },
    { text: '하나', value: '1' },
    { text: '둘', value: '2' },
    { text: '셋', value: '3' }
  ])
  const show = ref(false);

</script>

<script>

export default {
  name: 'basePractice',
  mounted() {
    // `this`를 통해 컴포넌트 인스턴스에 접근할 수 있습니다.
    console.log('mounted()가 호출 되었습니다:', this)
  }
}

</script>


<template>

  <div>
    <!-- 헤더 나누고 헤더 안에서 메뉴는 따로 비동기 로딩(defineAsyncComponent) -->
    <BaseHeader title="기본연습장"></BaseHeader>

    <main>
      <div>
        본문<br>
        <button type="button" id="s1" @click="fn_increase">함수카운트</button><span><strong>샘플{{ temp }}</strong></span><br>
        <button type="button" id="s2" @click="state.count++">클릭reactive카운트{{ state.count }}</button><br>
        <button type="button" id="s2" @click="map.get('count').value++">reactive Map 카운트{{ map.get('count').value }}</button>
        <button type="button" id="s2" @click="fn_selectList('test')">기본스크립트</button><br>
        <button type="button" id="s2" @click="fn_test">스크립트테스트</button><br>
        <button @click="show = !show">토글Show</button>
        <Transition>
          <p v-if="show">안녕</p>
        </Transition>

      </div>
      <div>
        <p>입력값 컨트롤</p>
        <p>메세지: {{ message }}</p>
        <input v-model="message" placeholder="메세지 입력하기" />

        <input type="checkbox" id="checkbox" v-model="checked" />
        <label for="checkbox">{{ checked }}</label>

        <div>체크된 이름: {{ checkedNames }}</div>
        <input type="checkbox" id="jack" value="젝" v-model="checkedNames">
        <label for="jack">젝</label>

        <input type="checkbox" id="john" value="존" v-model="checkedNames">
        <label for="john">존</label>

        <input type="checkbox" id="mike" value="마이크" v-model="checkedNames">
        <label for="mike">마이크</label>


        <div>선택한 것: {{ picked }}</div>

        <input type="radio" id="one" value="하나" v-model="picked" />
        <label for="one">하나</label>

        <input type="radio" id="two" value="둘" v-model="picked" />
        <label for="two">둘</label>

        <div>선택됨: {{ selected }}</div>

        <select v-model="selected">
          <option disabled value="">다음 중 하나를 선택하세요</option>
          <option>가</option>
          <option>나</option>
          <option>다</option>
        </select>
        <br>
        <div>선택됨: {{ selected2 }}</div>
        <select v-model="selected2">
          <option v-for="(option,index) in options" :value="option.value" :key="index">
            {{ option.text }}
          </option>
        </select>
      </div>
      <div>

        <p>v-for 샘플</p>
        <ul>일반
          <li v-for="(item, index) in items" v-bind:key="index">
            index:{{ index }} message: {{ item.message }}
          </li>
        </ul>

        <ul>객체값
          <li v-for="({ message }, index) in items" v-bind:key="index">
            message:{{ message }} index: {{ index }}
          </li>
        </ul>

        <ul>필터
          <li v-for="(item, index) in fileterArray" v-bind:key="index">
            message:{{ item.message }}
          </li>
        </ul>
        <button type="button" id="s2" @click="initArray">배열초기화</button><br>
        <ul>ref
          <li v-for="(item, index) in items2" v-bind:key="index">
            index:{{ index }} message: {{ item.message }}
          </li>
        </ul>

        <span v-for="n in 10" v-bind:key="n">{{ n }}</span>

      </div>
      <div>
        <p>v-if ,v-show 샘플</p>
        <button type="button" id="s2" @click="showFlag=!showFlag">showFlag</button><span v-if="showFlag">보여주기</span>
        <button type="button" id="s2" @click="showFlag=!showFlag">v-show</button><span v-show="showFlag">v-show보여주기</span>
      </div>
    </main>

    <BaseFooter></BaseFooter>
  </div>
</template>

<style>
</style>
